<template>
  <div v-for="task in tasks" :key="task.id">
    <Task 
      :task="task"  
      @deleteT="$emit('deleteT', task.id)"
      @toggleR="$emit('toggleR', task.id)"  
    />
  </div>
</template>

<script>
import Task from "./Task"

export default {
  name: 'Tasks',
  components: {
    Task
  },
  props: {
    tasks: Array
  },
  emits: [
    'deleteT',
    'toggleR'
  ]
}
</script>

