<template>
  <div @dblclick="$emit('toggleR', task.id)" :class="[task.reminder ? 'reminder' : '', 'container']">
   <div class="grid-container">
    <img :src="task.url" alt="avatar" class="image" height="50">
    <div class="task__title">
      <h1>{{task.text}}</h1>
      <p><i class="far fa-clock"></i> {{task.day}}</p>
    </div>
    <i @click="$emit('deleteT', task.id)" class="fas fa-times close"></i> 
   </div>
  </div>
</template>

<script>
export default {
  name: 'Task',
  props: {
    task: Object
  }
}
</script>

<style scoped>
h1{
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  color: #343a40;
}
img{
  border-radius: 50px;
}
p{
  font-size: 10px;
  font-weight: bold;
  color: #ccdbfd;
  font-family: 'Rajdhani', sans-serif;
}
.far{
  color: #ccdbfd;
}
.grid-container{
  display: grid;
  grid-template-columns: .75fr 2fr 1.7fr 0.4fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "image task__title . close";
}
.task__title{
  grid-area: task__title;
}
.image{
  grid-area: image;
}
.close{
  margin-top: 10px;
  grid-area: close;
}
.container{
  padding: 10px;
  background: #839b97;
  margin-bottom: 5px;
}
.reminder{
  border-left: 4px solid #40916c;
}
i{
  color: #e63946;
}
</style>