<template>
  <div class="container">
    <form class="form__container" @submit="addTask" action>
      <div class="text-control">
        <input
        type="text"
        v-model="text_task"
        name="text"
        placeholder="Add a task"
        />
      </div>
      
      <div class="date-control">
        <input
        type="text"
        v-model="date_task"
        name="day"
        placeholder="Date"
        />
      </div>
      
      <div class="date-control">
        <input
        @change="onFileChange"
        type="file"
        name="url"
        />
      </div>
      
      <div class="form__reminder">
        <label>Set Reminder :</label>
        <input type="checkbox" name="reminder" v-model="reminder"/>
      </div>
      <input class="submit" type="submit" value="Save Task" />
    </form>
    <hr>
  </div>
</template>

<script>
export default {
  name: "FormData",
  data() {
    return {
      text_task: '',
      date_task: '',
      url: null,
      reminder: false
    };
  },
  methods:{
    onFileChange(e) {
      const file = e.target.files[0];
      this.url = URL.createObjectURL(file);
    },
    addTask(e) {
      e.preventDefault()

      if(!this.text_task) {
        alert('plase Enter the task')
        return
      }

      const newTask = {
        // id: Math.floor(Math.random() * 10000),
        text: this.text_task,
        day: this.date_task,
        url: this.url,
        reminder: this.reminder
      }

      console.log(newTask)
      this.$emit('add-task', newTask)

      this.text_task = ''
      this.date_task = ''
      this.reminder = false
    } 
  }
};
</script>

<style scoped>
input[type="text"]{
  border: none;
  width: 95%;
  padding: 10px;
  color: #eeeeee;
  background: #40916c;
  margin-bottom: 10px;
  font-family: 'Rajdhani', sans-serif;
}
input::placeholder {
  color: #eeeeee;
}
input[type="checkbox"]{
  margin-bottom: 10px;
  margin-top: 30px;
}
hr{
  width: 95%;
  margin-top: 20px;
  margin-bottom: 20px;
  opacity: .5;
}
label{
  font-size: 18px;
  font-weight: 500;
  font-family: 'Oswald', sans-serif;
}
.fal{
  color: black;
}
.submit {
  width: 100%;
  color: #eeeeee;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 10px;
  background: #212529;
  font-family: 'Rajdhani', sans-serif;
}
.form__container{
  background: #f1faee;
  padding: 20px;
  margin-bottom: 10px;
  border-radius: 15px;
}
</style>
