<template>
  <div class="container">
    <Header @btn-show-hidde="showForm" :showFormData="showFormData" text="Tasks App" />
    <div v-show="showFormData">
      <FormData @add-task="addTask" />    
    </div>
    <Tasks 
      :tasks="tasks" 
      @deleteT="onDelete" 
      @toggleR="toggleReminder"
    />
  </div>
</template>

<script>
import Header from './components/Header'
import Tasks from './components/Tasks'
import FormData from './components/FormData'

export default {
  name: 'App',
  components: {
    Header,
    Tasks,
    FormData
  },
  data () {
    return {
      tasks: [],
      showFormData: false
    }
  },
  methods: {
      async addTask(task) {
        const res = await fetch('api/tasks', {
          method: 'POST',
          headers: {
            'Content-type': 'application/json'
          },
          body: JSON.stringify(task)
        })

        const data = await res.json()

        this.tasks = [...this.tasks, data]
      },
      async onDelete(id) {
        if(confirm('Are you sure ?')) {
          const res = await fetch(`api/tasks/${id}`, {
            method: 'DELETE',
          })
          
          res.status === 200 ? 
            (this.tasks = this.tasks.filter(
              (task)=> task.id !== id)) : 
              alert('Error Deleting task')

        }        
      },
      async toggleReminder(id) {
        const toToggle = await this.fetching(id) 
        const updTask = await {
          ...toToggle, 
          reminder: !toToggle.reminder
        }

        const res = await fetch(`api/tasks/${id}`, {
          method: 'PUT',
          headers: {
            'Content-type': 'application/json',
          },
          body: JSON.stringify(updTask)
        })

        const data = await res.json()

        this.tasks = this.tasks.map(
          (task) => 
            task.id === id ? {
              ...task, reminder: data.reminder
            } : task
        )
      },
      showForm(){
        this.showFormData = !this.showFormData
      },
      async fetchTasks() {
        const res = await fetch('api/tasks')
        
        const data = await res.json()

        return data
      },
      async fetching(id) {
        const res = await fetch(`api/tasks/${id}`)
        
        const data = await res.json()

        return data
      },
  },
  async created() {
    this.tasks = await this.fetchTasks()
  }
}
</script>

